import { Component, OnInit, Input, HostListener, ViewEncapsulation, ElementRef } from '@angular/core';
import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { TemplatePortal } from '@angular/cdk/portal';
import { MatButton } from '@angular/material/button';
import { Subject } from 'rxjs';

import { defaultTooltip } from 'src/app/data/mock/common/help-tooltip/data';

declare var $:JQueryStatic;

@Component({
  selector: 'help-tooltip',
  templateUrl: './help-tooltip.component.html',
  styleUrls: ['./help-tooltip.component.scss'],
  encapsulation  : ViewEncapsulation.None,
  exportAs       : 'help-tooltip'
})
export class HelpTooltipComponent implements OnInit {

  private _overlayRef: OverlayRef;
  private  _defaultTooltip: any;

  @Input() title:any;

  message:any;

  constructor(
    private _elementRef: ElementRef
  ) {

    this._defaultTooltip = defaultTooltip;

  }

  @HostListener('mouseenter')
  private _onMouseenter(): void
  {
    var elem = this._elementRef.nativeElement
    const hasClass = $(elem).find('.help-tooltip-content').hasClass('show');

    if(!hasClass)
    $(elem).find('.help-tooltip-content').addClass('show')
  }

  @HostListener('mouseleave', ['$event'])
  private _onMouseleave(event): void
  {
    var elem = this._elementRef.nativeElement
    const hasClass = $(elem).find('.help-tooltip-content').hasClass('show');

    if(hasClass)
    $(elem).find('.help-tooltip-content').removeClass('show')
  }

  ngOnInit(): void {

    let data = this._defaultTooltip.filter( item => {
      return item.title == this.title
    })

    if(data == undefined || data.length == 0){
      this.message = ""
    }
    else {
      this.message = data[0].text
    }

  }

}
